.products-hero {
    background-color: var(--primary-color);
    color: var(--background-color);
    padding: 4rem 0;
    text-align: center;
  }
  
  .products-hero h1 {
    color: var(--accent-color);
    font-size: 3rem;
    margin-top: 3.5rem;
  }
  
  .products-hero p {
    font-size: 1.25rem;
  }
  
  .products-content {
    padding: 4rem 0;
  }
  
  .products-filter {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 2rem;
  }
  
  .products-filter button {
    background: none;
    border: 2px solid var(--primary-color);
    color: var(--primary-color);
    padding: 0.5rem 1rem;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .products-filter button.active,
  .products-filter button:hover {
    background-color: var(--primary-color);
    color: var(--background-color);
  }
  
  .products-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
  }
  
  .product-card {
    background-color: var(--background-color);
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }
  
  .product-card:hover {
    transform: translateY(-5px);
  }
  
  .product-card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .product-card h3 {
    padding: 1rem;
    text-align: center;
  }
  
  .product-card .btn {
    display: block;
    width: 100%;
    padding: 0.75rem;
    border: none;
    border-radius: 0;
  }
  
  @media (max-width: 768px) {
    .products-hero h1 {
      font-size: 2.5rem;
    }
  
    .products-hero p {
      font-size: 1rem;
    }
  
    .products-content {
      padding: 2rem 0;
    }
  }